import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import axios from "axios";
import ErrorPop from "../components/ErrorPop";
import "../i18n";
import { useNavigate } from "react-router-dom";
function Pageverified() {
  const { t, i18n } = useTranslation();
  const currentYear = new Date().getFullYear();
  const [successPopMessage, setsuccessPopMessage] = useState("");
  const navigate = useNavigate();
  var apiUrl = "https://backend.amourette.no/api/members/";
  document.title = "Verfied Page";
  const handleSubmit = async (event) => {
    event.preventDefault(); // Prevent default form submission

    const formData = new FormData();
    formData.append("code", event.target.code.value);

    try {
      const res = await axios.post(apiUrl + "checkcodeverified", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });

      if (res.data.status === "2") {
        setsuccessPopMessage("Invalid code!");
        setTimeout(() => {
          setsuccessPopMessage("");
        }, 1500);
      } else {
        localStorage.removeItem("userLogin");
        sessionStorage.removeItem("userLogin");
        localStorage.removeItem("userLogin");
        localStorage.setItem(
          "codeverified",
          JSON.stringify(event.target.code.value)
        );
        navigate("/login");
      }
    } catch (err) {}
  };
  return (
    <>
      {successPopMessage && <ErrorPop message={successPopMessage} />}
      <div className="container d-flex justify-content-center align-items-center vh-100">
        <div
          className="card w-100"
          style={{ maxWidth: "500px", minHeight: "260px" }}
        >
          <div className="card-body text-center">
            <h2 className="card-title mb-4">
              Enter Code <span className="text-danger">*</span>
            </h2>
            <form
              action="javascript:void(0)"
              method="post"
              onSubmit={handleSubmit}
              className="mt-5 p-4"
            >
              <div className="input-group mb-3">
                <input
                  type="text"
                  required
                  className="form-control"
                  name="code"
                  placeholder="code"
                />
              </div>
              <button type="submit" className="btn btn-primary w-100">
                Submit
              </button>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}

export default Pageverified;
